import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		userName: '',
		menu: [{
				icon: 'iconfont el-icon-house',
				index: 'dashboard',
				title: '首页',
				status: true,
				state: true,
			},
			{
				icon: 'iconfont el-icon-coin',
				index: 'manage',
				title: '图片版权管理',
				status: true,
				state: true,
				subs: [{
					index: 'picture',
					title: '图片管理',
					icon: 'iconfont el-icon-picture-outline',
					status: true
				}, {
					index: 'uploading',
					title: '上传结果',
					icon: 'iconfont el-icon-picture-outline',
					status: true
				}]
			},
			{
				icon: 'iconfont el-icon-setting',
				index: 'system',
				title: '系统管理',
				status: true,
				state: true,
				subs: [{
					index: 'consumer',
					title: '用户管理',
					icon: 'iconfont el-icon-document',
					status: true
				}, 
				{
					index: 'enrolment',
					title: '注册',
					icon: 'iconfont el-icon-document',
					status: false
				}, 
				{
					index: 'character',
					title: '角色管理',
					icon: 'iconfont el-icon-document',
					status: true
				}, {
					index: 'menu',
					title: '菜单管理',
					icon: 'iconfont el-icon-document',
					status: true
				}]
			},
		],
	},
	mutations: {
		setuserName(state,params) {
			state.userName = params;
		},
		setstatus(state,params){
			state.menu[params.index].status = params.value;
		},
		setstatuss(state,params){
			state.menu[params.index].subs[params.indexs].status = params.value;
		}
	},
	actions: {},
	modules: {}
})
